import React from 'react';
import Search from '../components/search';
import Container from 'react-bootstrap/Container';

const MainPage = () => {
    return (
        <div>
            <div className='search-container d-flex align-items-center'>
                <Container>
                    <Search/>
                </Container>
            </div>
        </div>
    );
}

export default MainPage;
